.movie {
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    gap: 20px;
    color: inherit;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: all 0.5s ease-in-out;
}

.movie:hover {
    background-color: #adaeb9;
    color: white;
}

.movie__img {
    position: relative;
    top: -50px;
    max-width: 150px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title, .movie__year {
    margin: 0;
    font-weight: 300;
}

.movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: inherit;
}

.movie__genres {
    list-style: none;
    padding: 0;
    margin: 5px 0;
    display: flex;
    flex-wrap: wrap;
}

.movie__genres li, movie__year {
    margin-right: 10px;
    font-size: 14px;
}