.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    gap: 100px;
    padding: 70px 50px;
    width: 80%;
}

@media screen and (width <= 1090px) {
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}